/* exo-2-regular - latin */
@font-face {
  font-family: "Exo 2";
  font-style: normal;
  font-weight: 400;
  src: url("./exo-2-v9-latin/exo-2-v9-latin-regular.eot"); /* IE9 Compat Modes */
  src: local(""),
    url("./exo-2-v9-latin/exo-2-v9-latin-regular.eot?#iefix")
      format("embedded-opentype"),
    /* IE6-IE8 */ url("./exo-2-v9-latin/exo-2-v9-latin-regular.woff2")
      format("woff2"),
    /* Super Modern Browsers */
      url("./exo-2-v9-latin/exo-2-v9-latin-regular.woff") format("woff"),
    /* Modern Browsers */ url("./exo-2-v9-latin/exo-2-v9-latin-regular.ttf")
      format("truetype"),
    /* Safari, Android, iOS */
      url("./exo-2-v9-latin/exo-2-v9-latin-regular.svg#Exo2") format("svg"); /* Legacy iOS */
}
